import React from "react";
import logo from "./logo.svg";
import "antd/dist/antd.css";
import "./style.scss";
import "./App.css";
import { VPMap } from "./config/VPMap";
import {
  PageHeader,
  Radio,
  Input,
  Menu,
  Dropdown,
  Button,
  Tabs,
  Avatar,
} from "antd";
import ImageGallery from "react-image-gallery";

class App extends React.Component {
  setImages() {
    this.setState({
      images: [
        {
          original: VPMap[this.state.suit + "-" + this.state.int].open,
          thumbnail: VPMap[this.state.suit + "-" + this.state.int].open,
        },
        {
          original: VPMap[this.state.suit + "-" + this.state.int].personal,
          thumbnail: VPMap[this.state.suit + "-" + this.state.int].personal,
        },
        {
          original: VPMap[this.state.suit + "-" + this.state.int].roll,
          thumbnail: VPMap[this.state.suit + "-" + this.state.int].roll,
        },
      ],
    });
  }

  handleClick = (e) => {
    console.log("click");
    console.log(e.target.id);
    this.setState(
      {
        [e.target.id]: e.target.value,
      },
      () => {
        this.setImages();
      }
    );
  };

  handleAvatarClick = (e, id, val) => {

    console.log("clicked", id, val);
 
    this.setState(
      {
        [id]: val,
      },
      () => {
        this.setImages();
      }
    );
  };

  state = {
    activeSuit: "Black",
    activeInt: "FloralNavy",
    suit: "Black",
    int: "FloralNavy",
    images: [
      {
        original: VPMap["Black" + "-" + "FloralNavy"].open,
        thumbnail: VPMap["Black" + "-" + "FloralNavy"].open,
      },
      {
        original: VPMap["Black" + "-" + "FloralNavy"].personal,
        thumbnail: VPMap["Black" + "-" + "FloralNavy"].personal,
      },
      {
        original: VPMap["Black" + "-" + "FloralNavy"].roll,
        thumbnail: VPMap["Black" + "-" + "FloralNavy"].roll,
      },
    ],
  };
  render() {
    return (
      <div className="App">
        <div>
          <div className="config-container">
            <div className="mtl-btn-container" id="test">
              <b>Suit Material</b>
              <div>
                <Avatar
                  size={35}
                  onClick={(e) => this.handleAvatarClick(e, "suit", "Black")}
                  className={`suit-swatch`}
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/black-pindot.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) =>
                    this.handleAvatarClick(e, "suit", "BrightBlue")
                  }
                  className="suit-swatch"
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/bright-blue.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) =>
                    this.handleAvatarClick(e, "suit", "GreyPlaid")
                  }
                  className="suit-swatch"
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/grey-plaidpng.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) =>
                    this.handleAvatarClick(e, "suit", "Sharkskin")
                  }
                  style={{ filter: "brightness(1.75)" }}
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/sharkskin.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) => this.handleAvatarClick(e, "suit", "Wine")}
                  className="suit-swatch"
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/wine.png"
                />
              </div>
            </div>

            <div className="int-btn-container" style={{ marginBottom: "5px" }}>
              <b>Interior Material</b>
              <div>
                <Avatar
                  size={35}
                  onClick={(e) =>
                    this.handleAvatarClick(e, "int", "FloralNavy")
                  }
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/floral-navy.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) =>
                    this.handleAvatarClick(e, "int", "FloralPurple")
                  }
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/floral-purple.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) =>
                    this.handleAvatarClick(e, "int", "NavyWhiteDot")
                  }
                  style={{ filter: "brightness(1.75)" }}
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/navy-dot.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) => this.handleAvatarClick(e, "int", "Surfside")}
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/surfside.png"
                />

                <Avatar
                  size={35}
                  onClick={(e) => this.handleAvatarClick(e, "int", "Taupe")}
                  src="https://solutions-engineering.s3.amazonaws.com/luxury-goods-quickdemo/swatches/taupe.png"
                />
              </div>
            </div>
          </div>
          <ImageGallery
            items={this.state.images}
            showPlayButton={false}
            showFullscreenButton={false}
          />
        </div>
      </div>
    );
  }
}

export default App;
